.plan-section{
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 30px;
    flex-direction: column;
    background-color: white;
    position: relative;
}
.plan-card-container{
  width: 88%;
  flex-wrap: wrap;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.plan-card {
  position: relative;
  width: 320px;
  margin: 40px 10px;
  /* height: 420px; */
  padding: 20px;
  box-shadow: 0px 0px 30px #e1e6e7;
  background: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 25px;
  font-weight: bold;
  border-radius: 15px;
  cursor: pointer;
}

.plan-card::before,
.plan-card::after {
  position: absolute;
  content: "";
  width: 20%;
  height: 20%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 25px;
  font-weight: bold;
  background-color: var(--facebook-mild);
  transition: all 0.5s;
}
.plan-card-cover{
  z-index:3;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border-radius: 10px;
}
.plan-card::before {
  top: 0;
  right: 0;
  border-radius: 0 15px 0 100%;
}

.plan-card::after {
  bottom: 0;
  left: 0;
  border-radius: 0 100%  0 15px;
}

.plan-card:hover::before,
.plan-card:hover:after {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  transition: all 0.5s;
}

.plan-card:hover:after {
  content: "";
}
.plan-card-cover{
  justify-content: space-around;
}
.plan-card-header{
  padding: 20px 0px;
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #ddd;
}
.plan-card-header h2{
  font-size: 1.2rem;
  text-transform: capitalize;
  font-weight: 500;
  font-family: 'Montserrat';
}
.plan-card-header p{
  font-size: 1.5rem;
  color: #7f7f7f;
}
.plan-tab-container{
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
}
.plan-tab{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 5px 0px;
  padding: 5px 0px;
  border-bottom: 1px dashed #b4b4b4;
}
.plan-tab p{
  text-transform: capitalize;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: 450;
  letter-spacing: .5px;
  color: var(--secondary-color);
}
.buttons {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80%;
  height: 50px;
  border-radius: 50px;
  border: 0;
  outline: 0;
  font-size: 18px;
  background: var(--color-variant);
  color: #fff;
  cursor: pointer;
  margin-top: 10px;
}

svg {
  margin: 0 0 0 10px;
}

.buttons:hover svg {
  margin: 0 0 0 25px;
  transition: 0.3s;
}

.buttons:not(:hover) svg {
  transition: 0.3s;
}

.buttons:active {
  transform: scale(0.9, 0.9);
  transition: 200ms;
}

.buttons:not(:active) {
  transition: 200ms;
}

  @media (max-width: 990px) and (min-width: 768px){
    .plan-section{
       background-repeat: no-repeat;
    }
    
  }
  @media screen and (max-width: 767px){
    .my-plan-container{
      width: 100%;
      padding: 40px 0px;
    }
    .plan-card{
      width:100% ;
    }
    .container{
      width: 100%;
    }
    .plan-section{
      background-repeat: no-repeat;
   }
  }